import React from 'react';
import { HashRouter, Switch, Route } from "react-router-dom";
import NoMatch from './components/NoMatch';
import MultipleSelector from './components/MultipleSelector';
import DataPagers from './components/DataPagers';

function App() {
  return (
    <HashRouter basename='/'>
      <Switch>
        <Route path={'/react-data-pager/'} component={DataPagers} />
        <Route path={'/react-selector-multiple/'} component={MultipleSelector} />
        <Route path='*' component={NoMatch} />
      </Switch>
    </HashRouter>
  );
}

export default App;
