import React, { useState } from 'react';
import MultipleSelect from 'react-selector-multiple';

const MultipleSelector = (props) => {
    const [values_1, setValues_1] = useState([]);
    const [values_2, setValues_2] = useState([]);
    const [values_3, setValues_3] = useState([]);

    const options_1 = [
        'Red',
        'Green',
        'Blue',
        'Yellow'
    ];

    const options_2 = [
        { value: 'Red', name: 'Red' },
        { value: 'Green', name: 'Green' },
        { value: 'Blue', name: 'Blue' },
        { value: 'Yellow', name: 'Yellow' }
    ];

    const options_3 = [
        { value: { id: 1 }, name: 'Red' },
        { value: { id: 2, value: 'green' }, name: 'Green' },
        { value: { id: 3, value: 'blue' }, name: 'Blue' },
        { value: { id: 4, value: 'yellow' }, name: 'Yellow' }
    ];

    return (
        <div style={{ width: '100vw', height: '100vh', overflow: 'auto' }}>
            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <div>
                    <h4>options_1 array of strings:</h4>
                    <MultipleSelect
                        options={options_1}
                        selectedValues={values_1}
                        onChange={e => setValues_1(e)}
                        style={{ borderRadius: '5px', outline: 'none' }}
                    />
                </div>
                <div style={{ width: 'fit-content', height: 'fit-content', backgroundColor: '#d8d8d8', padding: '10px', borderRadius: '5px', marginTop: '5px' }}>
                    <pre>const [values_1, setValues_1] = useState([]);</pre>
                    <pre>
                        const options_1 = [
                        'Red',
                        'Green',
                        'Blue',
                        'Yellow'
                        ];
                 </pre>

                    <pre>
                        <div>{`<MultipleSelect `}</div>
                        <div>   {`options={options_1}`}</div>
                        <div>   {`selectedValues={values_1}`}</div>
                        <div>   {`onChange={e => setValues_1(e)}`}</div>
                        <div>   {`style={{ borderRadius: '5px', outline: 'none' }}`}</div>
                        <div>{`/>`}</div>
                    </pre>
                </div>

                <div>
                    <h4>options_2 array of objects, the value is string:</h4>
                    <MultipleSelect
                        options={options_2}
                        selectedValues={values_2}
                        onChange={e => setValues_2(e)}
                        style={{ borderRadius: '5px', outline: 'none' }}
                        selectedOptionStyle={{ color: '#dc143c' }}
                        placeHolder='Multiple Select'
                        selectedPlaceHolder={e => e.map((val, index) => e.length - 1 === index ? val : `${val}, `)}
                        selectAllOptions
                    />
                </div>
                <div style={{ width: 'fit-content', height: 'fit-content', backgroundColor: '#d8d8d8', padding: '10px', borderRadius: '5px', marginTop: '5px' }}>
                    <pre>const [values_2, setValues_2] = useState([]);</pre>
                    <pre>
                        <div>{`const options_2 = [`}</div>
                        <div>   {`{ value: 'Red', name: 'Red' },`}</div>
                        <div>   {`{ value: 'Green', name: 'Green' },`}</div>
                        <div>   {`{ value: 'Blue', name: 'Blue' },`}</div>
                        <div>   {`{ value: 'Yellow', name: 'Yellow' }`}</div>
                        <div>{`];`}</div>
                    </pre>

                    <pre>
                        <div>{`<MultipleSelect `}</div>
                        <div>   {`options={options_2}`}</div>
                        <div>   {`selectedValues={values_2}`}</div>
                        <div>   {`onChange={e => setValues_2(e)}`}</div>
                        <div>   {`style={{ borderRadius: '5px', outline: 'none' }}`}</div>
                        <div>   {`selectedOptionStyle={{ color: '#dc143c' }}`}</div>
                        <div>   {`placeHolder='Multiple Select'`}</div>
                        <div>   {`selectedPlaceHolder={e => e.map((val, index) => 
                        e.length - 1 === index ? val : \`` + '${val}, ' + `\`)}`}</div>
                        <div>   {`selectAllOptions`}</div>
                        <div>{`/>`}</div>
                    </pre>
                </div>

                <div>
                    <h4>options_3 array of objects, the value is object:</h4>
                    <MultipleSelect
                        options={options_3}
                        selectedValues={values_3}
                        onChange={e => setValues_3(e)}
                        style={{ borderRadius: '5px', outline: 'none' }}
                        selectedOptionStyle={{ color: '#dc143c' }}
                        placeHolder='Multiple Select'
                        selectedPlaceHolder={e => e.map((val, index) => e.length - 1 === index ? val.value ? val.value : val.id :
                            `${val.value ? val.value : val.id}, `)}
                        selectAllOptions
                    />
                </div>
                <div style={{ width: 'fit-content', height: 'fit-content', backgroundColor: '#d8d8d8', padding: '10px', borderRadius: '5px', margin: '5px 0 15px 0' }}>
                    <pre>const [values_3, setValues_3] = useState([]);</pre>
                    <pre>
                        <div>{`const options_3 = [`}</div>
                        <div>   {`{ value: { id: 1 }, name: 'Red' },`}</div>
                        <div>   {`{ value: { id: 2, value: 'green' }, name: 'Green' },`}</div>
                        <div>   {`{ value: { id: 3, value: 'blue' }, name: 'Blue' },`}</div>
                        <div>   {`{ value: { id: 4, value: 'yellow' }, name: 'Yellow' }`}</div>
                        <div>{`];`}</div>
                    </pre>

                    <pre>
                        <div>{`<MultipleSelect `}</div>
                        <div>   {`options={options_3}`}</div>
                        <div>   {`selectedValues={values_3}`}</div>
                        <div>   {`onChange={e => setValues_3(e)}`}</div>
                        <div>   {`style={{ borderRadius: '5px', outline: 'none' }}`}</div>
                        <div>   {`selectedOptionStyle={{ color: '#dc143c' }}`}</div>
                        <div>   {`placeHolder='Multiple Select'`}</div>
                        <div>   {`selectedPlaceHolder={e => e.map((val, index) => e.length - 1 === index ? 
                        (val.value ? val.value : val.id) :
                        \`` + '${val.value ? val.value : val.id}, ' + `\`)}`}</div>
                        <div>   {`selectAllOptions`}</div>
                        <div>{`/>`}</div>
                    </pre>
                </div>
            </div>
        </div>
    );
};

export default MultipleSelector;