import React, { useEffect } from 'react';

const NoMatch = (props) => {
    return (
        <section class='errorPage disableTextSelectionHighlighting'>
            <div className='errorPage__text'>
                <h3>404</h3>
            </div>
            <span className='errorPage__templeweed-container'>
                <img className='errorPage__tembleweed' />
            </span>
            <div className='errorPage__terrain'></div>
        </section>
    );
};

export default NoMatch;